// vuex
import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
// 
Vue.use(Vuex);

// Store
const store = new Vuex.Store({
  // 
  state: {
    // 
    topics: [{
        name: 'all',
        desc: ''
      },
      {
        name: 'good',
        desc: ''
      },
      {
        name: 'share',
        desc: ''
      },
      {
        name: 'ask',
        desc: ''
      },
      {
        name: 'job',
        desc: ''
      },
      {
        name: 'dev',
        desc: ''
      }
    ],
    // 
    isShowMoreMenus: false,
    isLogined: false,
    loginname:'',
    accesstoken:'',
    // 
    loginedMoreMenus: {
      create: '',
      center: '',
      msg: '',
      logout: ''
    },
    //
    isLogined: false,
    //
    loginname: '',
    //accesstoken
    mutationAccessToken: function(state,payload){
      state.accesstoken = payload;
    },
    // 
    unLoginedMoreMenus: {
      login: '',
    }
  },
  // state
  getters: {
    moreMenus(state) {
        if(state.isLogined) {
          return state.loginedMoreMenus;
        }
        return state.unLoginedMoreMenus;
      },
      getTopicDescByTab: function(state)
      {
        //  //
        return function(tab) { 
          for (var index = 0; index < state.topics.length; index++)
          {
            if (state.topics[index]
              .name == tab){
                return state.topics
                [index].desc; 
            }
          }
        };
      }
  },
  mutations,
  actions
});

export default store;



